.more-btn {
  cursor: pointer;
  color: #05c2df;
  transition: color .3s;
  position: relative;
}

.more-btn:hover {
  color: #fff;
}

.more-btn:after {
  content: "";
  border-top: 5px solid #05c2df;
  border-left: 4.5px solid #0000;
  border-right: 4.5px solid #0000;
  transition: border-top-color .3s;
  display: block;
  position: absolute;
  bottom: -7px;
  left: 50%;
  transform: translate(-50%, 100%);
}

.more-btn:hover:after {
  border-top-color: #fff;
}

@font-face {
  font-family: Inter;
  src: url("Inter-VariableFont_opsz,wght.25edf01c.ttf");
}

@font-face {
  font-family: Quantum;
  src: url("Quantum-400.5ea912ea.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}

.page {
  scroll-behavior: smooth;
  min-width: 320px;
  position: relative;
}

.page__aside {
  opacity: 0;
  pointer-events: none;
  background-color: #0000;
  transition: opacity .3s, background-color .3s, transform .3s .3s;
  transform: translateX(-120%);
}

.page__aside--active {
  pointer-events: all;
  opacity: 1;
  background-color: #000000b3;
  transition: transform .3s, background-color .3s .3s;
  transform: translateX(0);
}

.page__menu {
  z-index: 100;
  opacity: 0;
  pointer-events: none;
  transition: opacity .3s;
  position: absolute;
  top: 0;
}

.page__menu:before {
  content: "a";
  z-index: 0;
  background-color: #000000b3;
  width: 100%;
  height: 100vh;
  display: block;
  position: absolute;
}

@media (width <= 1279px) {
  .page__menu--active {
    opacity: 1;
    pointer-events: all;
  }
}

.page:has(.page__body aside[class*="--active"]) {
  overflow-y: hidden;
}

