.dropdown {
  font-family: Inter, sans-serif;
  font-size: 16px;
  font-weight: 500;
  line-height: 130%;
  position: relative;
}

@media (width <= 767px) {
  .dropdown {
    font-size: 14px;
  }
}

.dropdown__trigger {
  cursor: pointer;
  color: #05c2df;
  transition: color .3s;
  position: relative;
}

.dropdown__trigger:hover {
  color: #fff;
}

.dropdown__trigger:after {
  content: "";
  border-top: 5px solid #05c2df;
  border-left: 4.5px solid #0000;
  border-right: 4.5px solid #0000;
  transition: border-top-color .3s;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translate(calc(100% + 3px), -100%);
}

.dropdown__trigger:hover:after {
  border-top-color: #fff;
}

.dropdown__trigger--active {
  color: #fff;
}

.dropdown__trigger--active:after {
  border-top-color: #fff;
}

.dropdown__content {
  z-index: 3;
  opacity: 0;
  pointer-events: none;
  background-color: #191536;
  border-radius: 4px;
  width: max-content;
  transition: transform .3s, opacity .3s;
  position: absolute;
  overflow: hidden;
  transform: translateY(-25px);
  box-shadow: 0 0 19px -2px #05c2df;
}

.dropdown__content--active {
  pointer-events: auto;
  opacity: 1;
  transform: translateY(5px);
}

.dropdown__list {
  flex-direction: column;
  display: flex;
}

.dropdown__item {
  cursor: pointer;
  padding: 5px 20px;
  transition: background-color .3s;
}

.dropdown__item:hover {
  background-color: #110e25;
}

.dropdown__item--active {
  color: #05c2df;
  background-color: #110e25;
}

